.App {
  text-align: center;
}
/* I took that menu button style from https://www.w3schools.com/howto/howto_css_menu_icon.asp */

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: rgb(0, 0, 0);
  margin: 6px 0;
  transition: 0.4s;
}
.bar3{
  width: 20px;
}
#menubutton:hover .bar3{
  width: 35px;
}
.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
  transform: rotate(-45deg) translate(-9px, 6px) ;
  background-color: #ffffff;

}
.change .bar2 {
  opacity: 0;
  background-color: #ffffff;

}
.change .bar3 {
  width: 35px;
  -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
  transform: rotate(45deg) translate(-8px, -8px) ;
  background-color: #ffffff;
}

.menu-text:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 5px;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.menu-text:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
  margin-top: 2px;

}
#menubutton{
  position: absolute;
  top: 1rem;
  right: 2rem;
  z-index: 25;
}
#menubutton:hover{
  cursor: pointer;
}
.menu{
  transition: 1s;
  overflow: hidden;
  width: 0;
  height: 0;
  opacity: 1;
}
.change .menu{
  left: 0;
  width: 100vw;
  height: 100vh;
}
#kalp{
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  z-index: 25;
  fill: black;
  color: black;
  stroke: black;
  z-index: 26;
  display: flex;
}
#kalp:hover{
  cursor: pointer;
}
.menus{
  transition: 1s;
  overflow: hidden;
  width: 0;
  height: 0;
  opacity: 1;
}
.changes .menus{
  left: 0;
  width: 100vw;
  height: 100vh;
}
.foto{
  width: 375px;
  height: 667px;
  transform: rotate(-90deg);
}
.geridon{
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  z-index: 25;
}
.geridon:hover{
  cursor: pointer;
}